body {
  --page-width: 1200px;
  --cover-height: 500px;
  max-width: 1350px;
  margin: 0 auto;

  --theme-gray-6b: #2d2a34;
  --theme-gray-4b: #3f3944;
  --theme-gray-2b: #4e4642;
  --theme-gray-4h: #9f95ad;
  --theme-gray-6h: #b6adc4;

  --theme-green: #54efd1;
  --theme-blue: #42d2ea;
  --theme-magenta: #ea4ba6;
  --theme-orange: #ef8e2e;
  --background-color: #232028;
  background-color: var(--background-color);

  font-family: 'Nunito', 'Nunito Bold', 'Nunito ExtraBold', 'Nunito Light', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: 'Nunito Light';
  src: url(/homepage/static/media/NunitoSans-Light.3b696c5e.ttf);
  font-weight: 200;
  font-style: normal;
}

@font-face {
  font-family: 'Nunito Regular';
  src: url(/homepage/static/media/NunitoSans-Regular.4dac7051.ttf);
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Nunito Bold';
  src: url(/homepage/static/media/NunitoSans-Bold.ac72fe0f.ttf);
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Nunito ExtraBold';
  src: url(/homepage/static/media/NunitoSans-Black.1f8f5740.ttf);
  font-weight: 800;
  font-style: normal;
}

h1 {
  font-family: "Nunito Regular";
  -webkit-filter: drop-shadow(10px 10px 10px #1a1a1a);
